.burger__label {
  position: absolute;
  top: 2vh;
  right: 2vw;
  cursor: pointer;
  display: block;
  width: 40px;
  height: 28px;
  box-sizing: border-box;
  padding: 12px 0 12px 0;
  z-index: 2;
}

#menuToggle {
  display: none;
}

#menuToggle:checked ~ .menu-mobile {
  max-width: 100%;
  max-height: 600px;
  overflow: visible;
  transition: max-height .45s ease-in;
  transition: max-width .45s ease-in;
  z-index: 10;
}

.burger {
  display: block;
  width: 36px;
  height: 4px;
  border-radius: 2px;
  background-image: linear-gradient(-90deg, #036ad0 0%, #49c8b7 65%);
  position: relative;
}

.burger:before {
  content: '';
  position: absolute;
  top: -8px;
  display: block;
  width: 36px;
  height: 4px;
  border-radius: 2px;
  background-image: linear-gradient(60deg, #036ad0 0%, #49c8b7 65%);
  transition: transform .3s linear;
}

.burger:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -8px;
  width: 36px;
  height: 4px;
  border-radius: 2px;
  background-image: linear-gradient(-90deg, #036ad0 0%, #49c8b7 65%);
  transition: transform .3s linear;
}

#menuToggle:checked + .burger__label .burger {
  visibility: hidden;
}

#menuToggle:checked + .burger__label .burger:before {
  transform: rotate(40deg);
  top: 0;
  visibility: visible;
  transition: transform .3s linear;
}

#menuToggle:checked + .burger__label .burger:after {
  transform: rotate(-40deg);
  bottom: 0;
  visibility: visible;
  transition: transform .3s linear;
}

