  padding: 50px 0px 25px 60px;
  background-color: transparent;
}

.banner-consult .banner-consult__title {
  font-size: 30px;
  color: white;
}

.banner-consult .banner-consult__desc {
  font-size: 15px;
  line-height: 1.6;
  color: white;
}

.promo-city {
  height: 100%;
  max-height: 530px;
}

.promo-city .promo-city__bg {
  width: 100%;
  height: 100%;
  max-height: 530px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.promo-city .promo-city__block {
  text-align: right;
  margin-top: 2700px;
  margin-left: 200px;
  z-index: 2;
}

.promo-city .promo-city__title {
  font-size: 30px;
  line-height: 1.4;
  letter-spacing: 3px;
  text-transform: uppercase;
  margin-bottom: 14px;
  color: black;
}

.feedback {
  padding: 50px 0;
  position: relative;
  height: 100%;
  height: 485px;
}

.feedback .feedback__info__title {
  font-size: 30px;
  line-height: 1.4;
  text-transform: uppercase;
  color: black;
  padding-right: 150px;
  margin-bottom: 16px;
}

.feedback .feedback__info__desc {
  font-size: 15px;
  line-height: 1.4;
  color: black;
  text-align: left;
  padding-right: 10px;
}

.feedback .feedback__form {
  width: 100%;
  display: flex;
  max-width: 472px;
  max-height: 394px;
  flex-direction: column;
  padding: 50px 60px;
  box-shadow: 0px 5px 12px 0.65px rgba(79, 136, 191, 0.3);
}

.feedback .feedback__form .form__group {
  position: relative;
  margin-bottom: 14px;
}

.feedback .feedback__form .form__group:last-of-type {
  margin-bottom: 20px;
}

.feedback .feedback__form .form__label {
  position: absolute;
  top: 0;
  right: -16px;
  margin: 0;
  padding: 0;
  width: 100%;
  display: block;
  font-size: 13px;
  color: #979797;
  background-color: transparent;
  transition: transform .2s, font-size .2s ease-out;
  transform: translateY(10px);
  z-index: -1;
}

.feedback .feedback__form .form__input {
  text-indent: 6px;
  position: relative;
  display: flex;
  width: 100%;
  max-height: 46px;
  padding: 18px 8px 0px 8px;
  border: 1px solid #d7d7d7;
  font-size: 13px;
  color: #444444;
  background-color: transparent;
  transition: 0.2s all ease-in;
}

.feedback .feedback__form .form__input:focus {
  border: 1px solid #036ad0;
}

.feedback .feedback__form .form__input:focus ~ .form__label {
  font-size: 10px;
  transform: translateY(5px);
}

.feedback .feedback__form .form__input:not(:placeholder-shown) ~ .form__label {
  transform: translateY(5px);
  opacity: 0.4;
}

.feedback .feedback__form .checkbox__group {
  margin-bottom: 20px;
}

.feedback .feedback__form .checkbox__group .input__checkbox__text {
  font-size: 13px;
  color: #444444;
  padding-left: 20px;
}

.feedback .feedback__form .form__btn {
  max-width: 210px;
}

.feedback .input__checkbox {
  position: relative;
  opacity: 0;
  margin: 0;
  padding: 0;
  width: 0;
  height: 0;
}

.feedback .input__checkbox:checked + label::before,
.feedback .input__checkbox:not(:checked) + label::before {
  position: relative;
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid #036ad0;
  cursor: pointer;
}

.feedback .input__checkbox:checked + label::after {
  position: absolute;
  margin-top: 6px;
  margin-left: -14px;
  content: '';
  display: inline-block;
  height: 5px;
  width: 9px;
  border-left: 1px solid #036ad0;
  border-bottom: 1px solid #036ad0;
  transform: rotate(-45deg);
  cursor: pointer;
}

.footer {
  padding-top: 30px;
  width: 100%;
}

.footer .phone {
  text-align: left;
}

.footer .phone .phone__digits__link {
  font-size: 24px;
  color: white;
}

.footer .footer-map__list {
  text-align: left;
  margin-top: 5px;
