}

.footer .footer-map__list__item {
  margin-bottom: 4px;
}

.footer .footer-map__col {
  width: 47%;
}

.footer .footer-map__item__link {
  font-size: 15px;
  color: white;
  opacity: 0.5;
  white-space: wrap;
  transition: opacity 0.2s ease-in;
}

.footer .footer-map__item__link:hover {
  opacity: 1;
}

.footer .footer-map__list__title {
  display: block;
  font-size: 18px;
  color: white;
  margin-bottom: 18px;
}

.footer .footer-socials {
  max-width: 150px;
  margin-top: 5px;
  display: inline-block;
  text-align: right;
  margin-left: auto;
  color: white;
}

.footer .socials__link {
  color: white;
  transition: opacity 0.2s ease-in;
}

.footer .socials__link:hover {
  opacity: 0.5;
}

svg.svg-hidden-sprite {
  display: none;
}

.fa, .fab, .fas {
  color: white;
  font-size: 16px;
}

.fa:hover, .fab:hover, .fas:hover {
  color: black;
  transition: color 0.25s ease 0s;
}

